.deatil-content{
  position: relative;
}

.news-detail-img-header{
  width: 100%;
  height: 550px;
  object-fit: cover;
  object-position: top;
  filter: brightness(35%);
}


.current_news{
  position: absolute;
  top: 100px;
  left: 100px;
  width: 68%;
  height:inherit;
  display: flex;
  flex-direction: row;
}

.current_news_img{
  width: 300px;
  height: 300px;
  object-fit: cover;
}

.current_news_body{
  display: flex;
  flex-direction: column;
  padding: 0 1rem;
  color: white;
}

.current_news_title{
  font-size: 3rem;
}


@media (max-width: 992px) {
  .current_news_img{
    width: 300px;
    height: 300px;
  }

  .current_news{
    flex-direction: column;
    top: 1rem;
    left: 0;
    width: 100%;
    justify-content: center;
    align-items: center;
  }

  .current_news_body > p{
    text-align: center;
  }

  .current_news_title{
    font-size: 2rem;
    font-weight: 600;
  }
}
