.section-column, .section-row{
  display: flex;
  flex-wrap: wrap;
}

.section-row{
  flex-direction: row;
  justify-content: space-between;
}

.section-column{
    flex-direction: column;
    align-items: center;
}

.section-column .news-card{
  width: 100%
}

.news-card{
    width: 24%;
    margin-bottom: 2rem;
}

.news-section-title{
    font-family: 'Boogaloo', cursive;
    font-weight: bold;
    text-transform: uppercase;
    font-size: 2.4rem;
    margin-top: 2rem;
    margin-bottom: 0;
}

@media (max-width: 992px) {
    .news-section-title{
        font-size: 1.6rem;
        margin-top: 2rem;
    }

    .news-card{
        width: 48%;
        margin-bottom: 1rem;
    }
}


@media only screen and (max-width: 600px) {

    .news-section-row{
        flex-direction: column;
        margin-top: 0;
    }

    .news-card{
        width: 100%;
        margin-bottom: 1rem;
    }

}
