.battlepass_chapterseason{
    font-size: 1.3rem;
    font-family: 'Boogaloo', cursive;
    font-weight: bold;
    text-transform: uppercase;
    color: #0dcaf0;
}

.battlepass_title{
  font-family: 'Boogaloo', cursive;
  font-weight: bold;
  font-size: 4rem;
  color: #fff;
}

.battlePass_header{
  position: relative;
}

.chapter_season_info{
  position: absolute;
  top: 100px;
  left: 100px;
}

.battlePass_img{
  width: 100%;
  height: 400px;
  object-fit: cover;
  object-position: center;
  filter: brightness(50%);
}

.battlepass-page-content{
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;

}

.rewards-content{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.battlepass-reward-icon{
  width:200px;
  margin-top: 20px;
}

.rewards-page-number{
    font-size: 2rem;
    font-family: 'Boogaloo', cursive;
    font-weight: bold;
    text-transform: uppercase;
    color: #0d6efd;
}

@media (max-width: 992px) {
  .battlepass-reward-icon{
    width: 180px;
    margin-top: 10px;
  }

  .battlepass-video{
    margin-bottom: 10px;
  }
}

@media only screen and (max-width: 600px) {
  .battlepass-reward-icon{
    width: 120px;
    margin-top: 10px;
  }

  .battlepass_chapterseason, .rewards-title{
    font-size: 1.4rem;
  }

  .rewards-page-number{
    font-size: 1rem;
  }
}
